<template>
  <div class="wrapper">
    <cHeader/>
    <main class="c_main">
      <transition-group tag="div" class="pf_list" name="list" appear>
        <a
         class="pf_list_item"
         v-for="(pfList, index) in pfLists"
         :key="pfList.no"
         :class="'pf'+ index"
         @click="open(pfList)"
        >
          <div class="pf_list_thumb">
            <picture>
              <source media="(min-width: 600px)" :srcset="pfList.thumb01">
              <img :src="pfList.thumb02">
            </picture>
          </div>
          <div class="pf_list_detail">
            <div class="pf_list_title">{{ pfList.title }}</div>
            <div class="pf_list_date">released:{{ pfList.date}}</div>
          </div>
        </a>
      </transition-group>
    </main>
    <footer class="c_footer">
    </footer>
  </div>
</template>

<script>
import cHeader from '../components/header.vue'

export default {
  name: 'index',
  components: {
    cHeader
  },
  computed: {
    pfLists () {
      return this.$store.getters.items
    }
  },
  mounted() {
    const positionY = this.$store.getters.scrollY
    window.scrollTo(0, positionY)
  },
  methods: {
    open (pfList) {
      this.$router.push(`/item/${pfList.p_id}`)
      const scrollY = window.scrollY
      this.$store.dispatch('scrollPosition', scrollY)
    }
  }
}
</script>

